import React, { useState } from 'react';

const PadItem = (props) => {
    const [line, setline] = useState(false);

    const deleteitem=(e)=>{
        
        if(line==true){
            setline(false);
        }
        else {
            setline(true);        
        }
    }



    return(
        <>
            <ul >
                <li onClick={deleteitem} id="through" style={{textDecoration: line ? 'line-through':'none',listStyleType:"none",fontSize:'35px'}} className="ml-0 pd-0 primary draggable align-item-center">{props.item}</li>
            </ul>
        </>
    )
}

export default PadItem;