import React, { useState } from 'react';
import PadItem from "./padItem";
const Nppad = () => {

    const [data1, setdata1] = useState();
    const [data2, setdata2] = useState([]);
    const [line, setLine] = useState(false);
    const Inevent=(e)=>{
            setdata1(e.target.value);
            console.log(e.target.value);
    }

    const in2 = () =>{
            setdata2((olditems)=>{

                return[...olditems, data1];
            });

            setdata1('');    
    }

    const deleteiteme =(id)=>{
        setdata2((olditems)=>{
            return(olditems.filter((arr, index)=>{
                return(index!==id);
            }))
        })
        
    }
    return(

        <>

<div className="text-center mx-auto">

            <div className="text-center col-sm-5 mx-auto">
                <input type="text" name='stdata' className="form-control col-sm-4" onChange={Inevent} value={data1} placeholder="Add items"/>
                <button onClick={in2} className="btn btn-primary mt-3 pl-5 pr-5">submit</button>
            <div>
            </div>
          

                {data2.map((b,i)=>{
                    return(
                        <>
                            <PadItem  onSelect={'deleteIteme'} id={i} item={b}/>
                        </>
                    )
                })}
            
            </div>
</div>
        </>
    )
}

export default Nppad;